.carousel{
    position: relative;
    display: flex;
    width: 100%;
    height: 415px;
    border-radius: 25px;
    background-position: center;
    background-size: cover;
    width: 90%;
    margin: 43px auto;
    @media screen and (max-width: 650px){
        height: 200px;  
        margin: 20px auto;   
    }
    &_arrow{
        position: absolute;
        top: 50%;
        height: 15%;
        transform: translateY(-50%);
        &_right{
            right: 25px;
        }
        &_left{
            left: 25px;
        }
    }
    .slideCount{
        position: absolute;
        bottom: 25px;
        left: 50%;
        transform: translateX(-50%);
        color: white;
        font-weight: 500;
        @media screen and (max-width:768px) {
            display: none;
        }

    }
}